<template>
  <div>
    <div class="row">
      <div class="col-4">
        <h3 class="fw-bold">Tabs</h3>
      </div>
      <div class="col-6">
        <b-form-input size="lg" class="" placeholder="Search"></b-form-input>
      </div>
      <div class="col">
        <router-link to="/admin/tabs/new">
          <v-btn tile color="error" large>
            <v-icon left>
              mdi-plus
            </v-icon>
            CREATE TAB
          </v-btn>
        </router-link>
      </div>
    </div>
    <div class="my-5">
      <v-data-table :headers="headers" :items="desserts" sort-by="calories"
                    show-select class="elevation-1" v-model="checkedIds" >
        <template v-slot:top>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title class="text-h5 grey lighten-2" style="justify-content:space-between;">{{ formTitle }}
                  <v-icon  @click="close" class="text-h5">mdi-close</v-icon>
              </v-card-title>

              <v-card-text class="p-2">
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="12">
                      <v-text-field v-model="editedItem.title"
                                    label="Title (requried)" dense></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="12">

                      <v-autocomplete :items="categories" item-text="cat_name" item-value="id" label="Select Category" v-model="editedItem.cat_id" @change="getSelectedSubcategory()" dense ></v-autocomplete>
                      
                    </v-col>
                    <v-col cols="12" sm="6" md="12" class="pb-0">
                      <v-autocomplete :items="subcategories" item-text="subcat_name" item-value="id" label="Select Sub category" dense :no-data-text="customNoDataText" multiple
                      v-model="subids">
                      </v-autocomplete>
                      
                    </v-col>
                    <v-col cols="12" sm="6" md="12" class="py-0">
                      <v-checkbox v-model="editedItem.tab_status" label="Active" value="1"></v-checkbox>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-btn class="cred"  text @click="close">
                  Cancel
                </v-btn>
                <v-btn v-if="isLoading" class="cred">Loading...</v-btn>
                <v-btn v-else class="cred" text @click="updateTab">
                  Update
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this
                item?
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn  class="cred" text @click="closeDelete">Cancel</v-btn>
                <v-btn v-if="isLoading" class="cred">Loading...</v-btn>
                <v-btn v-else class="cred" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="deletemultipletabs" max-width="500px">
              <v-card>
                <v-card-title class="text-h6">Are you sure you want to delete these tabs?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn  class="cred" text @click="closeMultiDailoge">Cancel</v-btn>
                  <v-btn v-if="isLoading" class="cred">Loading...</v-btn>
                  <v-btn v-else class="cred" text @click="multipleTabIds" >OK</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
        </template>
        <template v-slot:[`item.action`]="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
          <v-icon small @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>
        <template v-slot:[`item.active`]>
          <v-icon small class="mr-2">
            mdi-check
          </v-icon>
        </template>
        <template v-slot:[`item.tab_status`]="{ item }">
          <div v-if="item.tab_status === '1'">
            <v-icon small class="mr-2">mdi-check</v-icon>
          </div>
          <div v-else>
            <v-icon small class="mr-2">mdi-close</v-icon>
          </div>
        </template>
        <template v-slot:footer>
          <div class="text-muted p-3">
            <v-btn  color="grey" @click="getSelectedIds">DELETE SELECTED</v-btn>
          </div>
        </template>
      </v-data-table>
      <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
            <template v-slot:action="{attrs}">
              <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
            </template>
          </v-snackbar>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default{
    name: "TabsView",
    components: {  },
    data: () => ({
        checkbox: true,
        dialog: false,
        dialogDelete: false,
        categories:[],
        subids: [],
        subcategories:[],
        snackbar: false,
        text: '',
        timeout: -1,
        isLoading: false,
        checkedIds:[],
        deletemultipletabs:false,
        customNoDataText: 'Please Select Category first!',
        Status:[
          {"id":1,"value":"Active"},
          {"id":0,"value":"Inactive"},
        ],
        headers: [
            { text: 'Title', value: 'title', },
            { text: 'Sub Category', value: 'subcatnames' },
            { text: 'Avtive', align: 'end', value: 'tab_status', sortable: false },
            { text: 'Actions', align: 'end', value: 'action', sortable: false },
        ],
        desserts: [],
        editedIndex: -1,
        editedItem: {
            id: '',
            title: "",
            cat_id: '',
            sub_cat_id: '',
            tab_status: '',
        },
        defaultItem: {
            name: '',
            email: "",
            phone: '',
            city: '',
            username: '',
            reviewcount: '',
            created_at: ''
        },
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? '' : 'Edit tab'
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        },
        dialogDelete(val) {
            val || this.closeDelete()
        },
    },

    created() {
        this.initialize();
        this.getCategoryAndSubcategories();
    },

    methods: {
        initialize() {
          axios.get("tabs").then( (resp) =>{
            this.desserts = resp.data.tabs;
          });
        },

        editItem(item) {
            this.subids = [];
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true

            axios.get('get/subcatids?id='+item.id)
            .then((resp) =>{
              for (let i = 0; i < resp.data.subcatids.length; i++) {
                this.subids.push(
                    resp.data.subcatids[i].id
                );
              }
            });
        },

        deleteItem(item) {
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.desserts.splice(this.editedIndex, 1)
            this.closeDelete()

            var param = {
              id : this.editedItem.id
            };
            axios.post("delete/tab", param).then( (resp) => {
              this.initialize();
              this.getCategoryAndSubcategories();
              this.snackbar = true;
              this.text = resp.data.message;
              this.dialogDelete = false;
              this.timeout = 3000;
            });
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        closeDelete() {
            this.dialogDelete = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
            this.isLoading = false;
        },

        save() {
            if (this.editedIndex > -1) {
                Object.assign(this.desserts[this.editedIndex], this.editedItem)
            } else {
                this.desserts.push(this.editedItem)
            }
            this.close()
        },

        // To get categories and subcategories ( code start )
        getCategoryAndSubcategories(){
          axios.get("categories/subcategories").then( (resp) =>{
            this.categories = resp.data.categories;
            this.subcategories = resp.data.subcategories;
          });
        },
        // To get categories and subcategories ( code end )
        
        // To get categories and subcategories ( code start )
        getSelectedSubcategory(){
          axios.get("subcategories/"+this.editedItem.cat_id).then( (resp) =>{
            this.subcategories = resp.data;
          });
        },
        // To get categories and subcategories ( code end )

        //  To update the tabs details ( code start )
        updateTab(){
          this.isLoading = true;
          var params = {
            id: this.editedItem.id,
            title : this.editedItem.title,
            cat_id : this.editedItem.cat_id,
            subcat_id : this.subids,
            tab_status : this.editedItem.tab_status,
          };

          axios.post('update/tab', params)
          .then((resp) => {
            this.dialog = false;
            this.initialize();
            this.getCategoryAndSubcategories();
            this.snackbar = true;
            this.text = resp.data.message;
            this.timeout = 3000;
            this.isLoading = false;
          });

        },
        //  To update the tabs details ( code end )

        getSelectedIds() {
          var data = {
          ids:this.checkedIds.map(item => item.id)
        };
        if(data.ids.length == 0){
          this.snackbar = true;
          this.text = "Please select tabs!";
          this.timeout = 3000;
        }else{
        this.deletemultipletabs = true;
        }
      },
      
      closeMultiDailoge(){
        this.deletemultipletabs = false;
      },

      // Delete multiple tabs ( code start )
      multipleTabIds(){
        this.isLoading = true;
        var data = {
          ids:this.checkedIds.map(item => item.id)
        };
        axios.post('delete/multiple/tabs', data)
          .then((resp) => {
            if(resp.data.success == true){
              this.isLoading = false;
              this.initialize();
              this.deletemultipletabs = false;
              this.snackbar = true;
              this.text = resp.data.message;
              this.timeout = 3000;
            }else{
              this.isLoading = false;
              this.deletemultipletabs = false;
              this.initialize();
              this.snackbar = true;
              this.text = 'something went wrong';
              this.timeout = 3000;
            }
          });
      }
      // Delete multiple tabs ( code end )
    },
}
</script>